img.logo {
    margin-top: 0px;
    height: 70px;
  
  }
  .navbar {
      background: white;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.2rem;
      position: sticky;
      top: 0;
      z-index: 999;
      box-shadow: 0px 8px 8px -6px rgba(181, 181, 181);
    }
    
    .navbar-container {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 80px;
      max-width: 1500px;
    }
    
    .fa-typo3 {
      margin-left: 0.5rem;
      font-size: 1.8rem;
    }
    
    .nav-menu {
      display: grid;
      grid-template-columns: repeat(4, auto);
      grid-gap: 10px;
      list-style: none;
      text-align: center;
      width: 60vw;
      justify-content: end;
      margin-right: 2rem;
    }
    
    .nav-item {
      height: 80px;
    }
  
    a:hover {
      color: black;
    }
    
    .nav-links {
      color:black;
      display: flex;
      align-items: center;
      text-decoration: none;
      padding: 0.5rem 1rem;
      height: 100%;
    }
    
    .nav-links:hover {
      border-bottom: 4px solid #DE5D81;
      height: 80px;
      transition: all ease-out;
    }
    
    .fa-bars {
      color: black;
    }
    
    .nav-links-mobile {
      display: none;
    }
    
    .menu-icon {
      display: none;
    }

    #cart-span {
      padding: 0 3px;
      background-color: #DE5D81;
      font-weight: bold;
      border-radius: 5px;
      position: relative;
      top: -15px;
    }
    
    @media screen and (max-width: 960px) {
      .NavbarItems {
        position: relative;
      }
    
      .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
      }
    
      .nav-menu.active {
        background: white;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
      }
    
      .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
      }
    
      .nav-links:hover {
        color: #DE5D81;
        border-radius: 0;
      }
    
      .navbar-logo {
        position: absolute;
        top: 5px;
        left: 5px;
      }
    
      .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
      }
    
      .fa-times {
        color: black;
        font-size: 2rem;
      }
    
      .nav-links-mobile {
        display: block;
        text-align: center;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        text-decoration: none;
        font-size: 1.5rem;
        background-color: transparent;
        color: black;
        padding: 14px 20px;
        border: 1px solid black;
        transition: all 0.3s ease-out;
      }
    
      .nav-links-mobile:hover {
        background: white;
        color: black;
        transition: 250ms;
      }
    }